<template>
  <div class="upload-page">
    <div ref="imgContainer" id="imgContainer">
      <span>宇视十周年生日快乐</span>
      <div>
        <span>姓名</span>
        <input type="text" v-model="name">
      </div>
      <div>
        <span @click="changDate">相识日期</span>
        <input type="text" v-model="missDate">
      </div>
      <div class="chooseImg">
        <img src="../assets/image/camera.png" alt="" @click="selectImage" v-if="!imgSrc">
        <img :src="imgSrc" alt="" v-else>
      </div>



    </div>
    <div @click="getCanvas">点击生成</div>

    <div class="result">
      <img :src="imgUrl" alt="">
    </div>
  </div>

</template>

<script>


  import html2canvas from 'html2canvas';

  export default {
    name: "getPoster",
    data() {
      return {
        name: '',
        imgSrc: '',
        imgUrl: '',
        missDate: '',
        pickerVisible: false
      }
    },
    mounted() {
    },
    methods: {
      changeImg(e) {
        let _this = this
        var files = e.target.files[0]
        if (!e || !window.FileReader) return  // 判断是否支持FileReader
        let reader = new FileReader()
        reader.readAsDataURL(files) // 文件转换
        reader.onloadend = function () {
          _this.imgSrc = this.result
          _this.textHide = false;
        }
      },
      getCanvas() {
        const w = document.getElementById('imgContainer').offsetWidth
        const h = document.getElementById('imgContainer').offsetHeight
        html2canvas(this.$refs.imgContainer, {
          // 转换为图片
          width:w/2,
          height:h/2,
          scale:window.devicePixelRatio,
          useCORS: true // 解决资源跨域问题
        }).then(canvas => {
          console.log(canvas, 'canvas');
          let dataURL = canvas.toDataURL('image/png',1.0);
          this.showImg = true;
          this.imgUrl = dataURL;
        });
      },

      changDate() {
        this.$emit('changeDate')
      },
      selectImage() {
        var fileInput = document.getElementById("fileInput");
        if (fileInput) {
          fileInput.parentNode.removeChild(fileInput);
          fileInput = null;
        }
        if (fileInput == null) {
          fileInput = document.createElement("input");
          fileInput.id = "fileInput";
          fileInput.type = "file";
          fileInput.accept = "image/*";
          fileInput.style.height = "0px";
          fileInput.style.display = "block";
          fileInput.style.overflow = "hidden";
          console.log('lll')
          document.body.insertBefore(fileInput, document.body.firstChild);
          // if (navigator.userAgent.match(/Android/i)) {
          //     fileInput.addEventListener('change', tmpSelectFile, false);
          // } else {
          fileInput.addEventListener('change', this.selectFileImage, false);
          // }
        }
        setTimeout(function () {
          fileInput.click()
        }, 100);
      },
      selectFileImage(e){
        let _this = this
        var files = e.target.files[0]
        if (!e || !window.FileReader) return  // 判断是否支持FileReader
        let reader = new FileReader()
        reader.readAsDataURL(files) // 文件转换
        reader.onloadend = function () {
          _this.imgSrc = this.result
          _this.textHide = false;
        }

      }
    }
  }
</script>

<style scoped>
  .upload-page {
    font-size: 0.16rem;
    padding: 100px 0;
  }

  span {
    font-size: 0.36rem;
  }

  .datePicker {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

  }

  .chooseImg {
    width: 120px;
    height: 120px;
    margin: auto;
  }

  .chooseImg img {
    width: 120px;
    height: 120px;
  }
  .result{
    position: fixed;
    width: 300px;
    height: 500px;
  }
  .result img{
    /*width: 300px;*/
    /*height: 300px;*/
  }

</style>
